let burger = document.querySelector('.burger');
let openBurgerBtn = document.getElementById('open-burger')
let closeBurgerBtn = document.getElementById('close-burger')

const openMenu = (menu) => {
  menu.style = "display: flex";
}

const closeMenu = (menu) => {
  menu.style = "display: none";
}

openBurgerBtn.addEventListener('click', () => openMenu(burger));
closeBurgerBtn.addEventListener('click', () => closeMenu(burger));